.conAunti{
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.Aunti{
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 20px;
}
.Aunti h1{
  font-family: 'Roboto';
  color: aliceblue;
  text-shadow: 0 0 4px black;
}
.Aunti input{
  border: 0;
  padding: 5px 20px;
  box-shadow: 0 2px 4px black;
  border-radius: 5px;
  outline: 0;
  transition: all.3s ease-in-out;
}
.Aunti input:focus{
  box-shadow: 0 4px 4px 2px black;
  transform: translateY(-2px);
}
.Aunti input::placeholder{
  transition: all.2s ease-in-out;
}
.Aunti input:focus::placeholder{
  opacity: 0;
  transform: translateX(100px);
}
.Aunti button{
  user-select: none;
  transition: all.3s ease-in-out;
  padding: 5px 30px;
  border: 0;
  outline: 0;
  background: none;
  border-radius: 10px;
  box-shadow: 0 2px 4px black;
}
.Aunti button:hover{
  transform: translateY(-2px);
  box-shadow: 0 4px 0px  black;
  background: rgb(201, 201, 201);
  
}
.Aunti button:active{
  transition: all.1s ease-in-out;
  transform: translateY(2px);
  box-shadow: 0 0px 0px  black;
}
.alert{
  border-radius: 10px;
  user-select: none;
  position: absolute;
  bottom: 200px;
  padding: 20px;
  border: 1px solid;
  transition: all.5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  transform: translateY(50px);
}
.openalert{
  transform: none;
  opacity: 1;
  visibility: visible;
  
}
.Aunti label{
  position: relative;
}

.inputErr{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
  transition: all.3s ease-in-out;
}
.openErr{
  background: white;
  

}
.conDataTable{
  flex-grow: 1;
  overflow-x: hidden;
}