.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
select{
  border: 0;
  outline: 0;
}
.title{
  display: flex;
  padding: 10px 50px;
  justify-content: space-between;
}
:where(.title, .con_create, .info, .cell) button {
  user-select: none;
  transition: all.3s ease-in-out;
  padding: 5px 30px;
  outline: 0;
  background: none;
  border-radius: 10px;
  box-shadow: 0 2px 4px black;
  border: 1px solid white;
}
:where(.title, .con_create, .info, .cell) button:hover{
  transform: translateY(-2px);
  box-shadow: 0 4px 0px  black;
  border: 1px solid black;
  
}
:where(.title, .con_create, .info, .cell) button:active{
  transition: all.1s ease-in-out;
  transform: translateY(2px);
  box-shadow: 0 0px 0px  black;
}
:where(.title,.con_create,.info,.cell) button:disabled{
  pointer-events: none;
  color: black;
}
.formCon{
  z-index: 1;
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.514);
  animation: formcon 0.3s ease-in-out;
}
.conClose{
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
}
.formCreate{
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  box-shadow: 0 4px 4px black;
  animation: form 0.3s ease-in-out;
}
@keyframes formcon {
  0% {
    background: none;
  }
}
@keyframes form {
  0% {
    transform: translateY(-100px);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}
.infoCon{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
.con_create{
  display: flex;
  gap: 10px;
  margin: 0 0 10px 0;
}
:where(.con_create,.info) input{
  border: 0;
  padding: 5px 20px;
  box-shadow: 0 2px 4px black;
  border-radius: 5px;
  outline: 0;
  transition: all.3s ease-in-out;
}
:where(.con_create,.info) input:focus{
  box-shadow: 0 4px 4px 2px black;
  transform: translateY(-2px);
}
:where(.con_create,.info) input::placeholder{
  transition: all.2s ease-in-out;
}
:where(.con_create,.info) input:focus::placeholder{
  opacity: 0;
  transform: translateX(100px);
}
.info{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.info.create{
  row-gap: 5px;
}
.info.setting{
  row-gap: 20px;
}
.info button{
  grid-column: 1/3;
  align-self: center;
  justify-self: center;
}
.datatable{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  row-gap: 30px;
}
.cell{
  align-self: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 100px 200px;
  row-gap: 3px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 4px ;
}
.cell button{
  grid-column: 1/3;
}

.inputNumber{
  display: flex;
  gap: 20px;
}